.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
    overflow: hidden;
}

.tui-pagination {
    padding-bottom: 40px;
    margin: 0;
    line-height: 500;
    text-align: center;
    font-size: 0;

    @include mq(tablet){
        padding-bottom: 60px;
    }
}

.tui-pagination .tui-page-btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 37px;
    width: 37px;
    padding: 8px 0 6px;
    margin-left: 0;
    color: #000;
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    text-decoration: none;
    vertical-align: middle;
    // border: 1px solid #ddd;
    border-width: 1px 0;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);

    @include mq (tablet) {height: 40px;
        width: 40px;}
}

.tui-pagination .tui-page-btn:hover {
    background-color: #FF6B08;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
    color: #fff;
    background: #FF6B08;
    border-color: #FF6B08;
    cursor: default;
}

.tui-pagination .tui-is-selected:hover {
    background-color: #FF6B08;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
    margin-left: 0
}

.tui-ico-ellip {
    padding-bottom: 16px;
}

.tui-pagination .tui-first-child.tui-is-selected {
    border-left: 1px solid #FF6B08
}

.tui-pagination .tui-last-child.tui-is-selected {
    border-right: 1px solid #FF6B08
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    padding: 0;
    font-size: 0;
    line-height: 26px;
    white-space: nowrap;
    font-weight: 500;
    background: #F7F7F7;
}

.tui-pagination .tui-prev-is-ellip {
    border-right: 0
}

.tui-pagination .tui-next-is-ellip {
    border-left: 0
}

.tui-pagination .tui-first+.tui-prev,
.tui-pagination .tui-next+.tui-last {
    border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
    background: url(data:image/png;base64,) no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    padding: 0;
    line-height: 1;
}

.tui-pagination .tui-prev {
    margin-right: 10px;
}

.tui-pagination .tui-next {
    margin-left: 10px;
}

.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px
}

.tui-pagination .tui-ico-prev {
    width: 6px;
    height: 10px;
    background-position: -26px 0px
}

.tui-pagination .tui-ico-next {
    width: 6px;
    height: 10px;
    background-position: -13px -24px
}

.tui-pagination .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px
}

.tui-pagination .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 16px;
    vertical-align: top;
    background-position: 0px -24px
}

.tui-pagination .tui-is-disabled:hover {
    background: #fff;
    cursor: default
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
    background-position: -26px -12px
}

.tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px
}

.tui-pagination .tui-is-disabled .tui-ico-next {
    background-position: -21px -24px
}

.tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px
}

.tui-page-inputs {
    display: inline-block;
    vertical-align: top;
}

.tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 10px;
    height: 12px;
    padding: 8px 0 6px;
    font-size: 12px;
    text-align: center;
    color: #333;
    border: 0;
    outline: none;
}

.tui-page-inputs .tui-page-current {
    color: #FF6B08;
}

.tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 8px 0 6px;
    vertical-align: top
}

.tui-page-btn.tui-first,
.tui-page-btn.tui-last {
    display: none;

    @include mq (tablet) {
        display: inline-flex;        
    }
}

.tui-page-btn.tui-next-is-ellip.tui-last-child,
.tui-page-btn.tui-prev-is-ellip.tui-first-child {
    background-color: transparent;
    display: none;

    @include mq (tablet) {
                display: inline-flex;
            }
    
    &:hover {
        background-color: #FF6B08;
    }
}

.tui-page-btn.tui-first {
    margin-right: 10px;
}

.tui-page-btn.tui-last {
    margin-left: 10px;
}

.tui-page-btn.tui-is-disabled.tui-prev,
.tui-page-btn.tui-is-disabled.tui-next,
.tui-page-btn.tui-is-disabled.tui-first,
.tui-page-btn.tui-is-disabled.tui-last {
    background-color: white;
}
